	module.exports = {"data":{"post":{"html":"<p>Much to my surprise, our DSL connection was activated on schedule today and the splitter arrived with today's post. Alas, our ISDN NTBA and DSL splitter are in our basement washroom, where I have no ethernet cable, so we can surf from the washing machine but not from our desktop PCs. I've put our WLAN access point in our entryway under the coat rack, with a cable to the basement, so we can at least use our notebooks until I can cable things correctly.</p>","fields":{"title":"DSLed","date":"18 Jun 2004","url":"https://papascott.de/2004-06-18-dsled/"}}},"pathContext":{"slug":"/2004-06-18-dsled/","prev":{"fields":{"title":"Where I Won't Be","date":"17 Jun 2004","slug":"/2004-06-17-where-i-wont-be/"}},"next":{"fields":{"title":"Null Number","date":"19 Jun 2004","slug":"/2004-06-19-null-number/"}}}}

